.App {
  width: 100%;
  max-width: 1600px;
  margin: 0px auto; }

.container {
  margin: 0px;
  padding: 0px;
  width: 100%; }

.wrapper {
  position: relative;
  max-width: 1600px;
  margin: 0 auto;
  padding: 0px 200px; }
  @media screen and (max-width: 1440px) {
