.card {
  position: relative;
  width: calc(25% - 10px);
  height: 200px;
  padding: 0px;
  margin: 5px;
  border: solid 0.5px #eeeeee;
  border-radius: 2px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }
  .card::before {
    background: none; }
  .card h2 {
    font-size: 24px;
    font-weight: 500;
    color: #333333;
    margin: 6px 0px; }
  .card h3 {
    font-size: 20px;
    font-weight: 400;
    margin: 6px 0px;
    color: #5c5c5c; }
  .card .demo-type {
    width: 100%;
    height: 20px;
    line-height: 20px;
    font-size: 13px;
    text-align: center;
    background-color: #0eaba9;
    color: #ffffff;
    border-radius: 2px 2px 0px 0px;
    text-transform: uppercase;
    letter-spacing: 0.4px; }
  .card .content {
    padding: 0px 16px; }
  .card .demo-description {
    color: #333333;
    padding: 6px 0px; }
  .card .demo-link {
    position: absolute;
    bottom: 16px;
    left: 16px;
    margin: 6px 0px; }
    .card .demo-link a {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-align: center;
          align-items: center;
      font-size: 18px;
      font-weight: 400;
      color: #1781e3;
      outline: none;
      text-decoration: none; }
      .card .demo-link a:hover {
        color: #0851da; }
  @media screen and (max-width: 1600px) {
    .card {
      width: calc(25% - 10px); } }
  @media screen and (max-width: 1440px) {
    .card {
      width: calc(33% - 10px); } }
  @media screen and (max-width: 1280px) {
    .card {
      width: calc(50% - 10px); } }
  @media screen and (max-width: 900px) {
    .card {
      width: calc(50% - 10px); } }
  @media screen and (max-width: 750px) {
    .card {
      width: calc(100% - 10px); } }

